import _ from 'lodash'

export const dictionary = new Map([
    [/^en/, new Map([
        ['', 'OK'],
        ['', 'Cancel'],
        ['', 'Bold'],
        ['', 'Insert code'],
        ['', 'Language'],
        ['', 'Unselected'],
        ['', 'Fullscreen'],
        ['', 'Exit fullscreen'],
        [/^(\d)$/, (text, match) => `Header ${match[1]}`],
        ['', 'Hide preview'],
        ['', 'Show preview'],
        ['', 'Horizontal rule'],
        ['', 'Insert image'],
        ['', 'Image URL'],
        ['', 'Image title'],
        ['', 'About'],
        ['', 'Italic'],
        ['', 'Insert hyperlink'],
        ['', 'Link URL'],
        ['', 'Link title'],
        ['', 'Ordered list'],
        ['', 'Disable scroll sync'],
        ['', 'Enable scroll sync'],
        ['', 'Strikeout'],
        ['', 'Insert table'],
        ['', 'Number of rows'],
        ['', 'Number of columns'],
        ['', 'Alignment'],
        ['', 'Flush left'],
        ['', 'Centered'],
        ['', 'Flush right'],
        ['', 'Unordered list'],
        [' Markdown ', 'is an open-source Markdown editor for the modern web.'],
        [' GitHub ', 'View it on GitHub']
    ])],
    [/^zh/, new Map([
        [/^(\d)$/, (text, match) => `${''[match[1] - 1]}`]
    ])]
])

export function getText (text) {
    for (const language of navigator.languages) {
        for (const [langReg, textMapping] of dictionary) {
            if (langReg.test(language)) {
                let result
                let match = null
                if (textMapping.has(text)) {
                    result = textMapping.get(text)
                } else {
                    for (const [re, res] of textMapping) {
                        if (_.isRegExp(re)) {
                            match = re.exec(text)
                            if (match !== null) {
                                result = res
                                break
                            }
                        }
                    }
                }
                if (typeof result !== 'undefined') {
                    if (typeof result === 'function') {
                        result = result(text, match === null ? undefined : match)
                    }
                    return result
                } else {
                    return text
                }
            }
        }
    }
    return text
}
