export default {
    name: 'link',
    icon: 'fa-link',
    title: '',
    action: {
        request: {
            title: '',
            type: 'form',
            body: [
                {
                    name: 'url',
                    title: '',
                    type: 'dialog-input',
                    default: ''
                },
                {
                    name: 'description',
                    title: '',
                    type: 'dialog-input',
                    default: ''
                }
            ],
            callback (data) {
                return '[' + data.description + '](' + data.url + ')'
            }
        }
    }
}
